@import "../../styles/sizes";

.markdown {
  h1, h2, h3, h4, h5, p, em, code, li, span, th, tr {
    color: #08234a !important;
  }

  strong {
    font-weight: bolder;
  }

  h1 { font-size: 2.23125rem !important; }
  h2 { font-size: 1.7rem !important; }
  h3 { font-size: 1.5rem !important; }
  h4 { font-size: 1.25rem !important; }
  h5 { font-size: 16px !important; }
  h6 { font-size: .875rem !important; }

  @media screen and (max-width: $s) {
    padding-top: 128px !important;
  }

  table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    margin-bottom: 20px;
  }

  * + table {
    margin-top: 20px;
  }

  table th {
    padding: 16px 12px;
    text-align: left;
    vertical-align: bottom;
    text-transform: uppercase;
  }

  table td {
    padding: 16px 12px;
    vertical-align: top;
  }

  table td > :last-child {
    margin-bottom: 0;
  }

  table tfoot {
    font-size: 0.875rem;
  }

  table,
  table td {
    vertical-align: middle !important;
  }

  table > tr:not(:first-child),
  table > :not(:first-child) > tr,
  table > :first-child > tr:not(:first-child) {
    border-top: 1px solid whitesmoke;
  }
}

#sidenav-article {
  z-index: 100;
  background-color: #08234A;

  ul {
    min-width: 196px;
  }

  @media screen and (max-width: $s) {
    ul {
      max-width: 196px;
    }
  }

  ul li a:hover {
    background-color: rgba(0, 0, 0, .16);
  }

  li.uk-active a {
    color: whitesmoke !important;
    //color: rgba(255,255,255,.16) !important;
  }

  li {
    background-color: #08234A;
    height: $ratio;
    overflow: hidden;
    position: relative;
  }

  a {
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;
    color: rgba(255,255,255,.50) !important;
    font-size: 2em;
  }

  a span {
    font-family: 'Open Sans', sans-serif !important;
    position: relative;
    width: 100%;
    height: 100%;
    display: inline-block;
    top: $ratio / 2;
    transform: translate(0, -1em);
  }
}